<template>
    <div class="main-page">
        <input @change="selectedFile" type="file" name="file">
        <button @click="upload" type="submit"></button>
    </div>
</template>

<style>
</style>

<script>
//WIP
export default {
    data: function() {
        return {
            uploadFile: null
        }
    },
    methods: {
        selectedFile: function(e) {
            console.log(`----`);
            //  File 
            e.preventDefault();
            let fileData = e.target.files[0];
            console.log(fileData);

            let reader = new FileReader();
            
            //
            reader.onerror = () => console.log(``); 
            
            // 
            reader.onload = function() {
                let lineArr = reader.result.split("\n"); // 
                let itemArr = []; // 
                for (let i = 0,len = lineArr.length; i < len; i++) itemArr[i] = lineArr[i].split(",");
    
                // table
                let insert = '<table>';
                for (let i = 0, len = itemArr.length; i < len; i++) {
                    insert += '<tr>';
                    for (let j = 0, len = itemArr[i].length; j < len; j++) {
                        insert += '<td>';
                        insert += itemArr[i][j];
                        insert += '</td>';
                    }
                    insert += '</tr>';
                }
                insert += '</table>';
                console.log(insert);
                // result.innerHTML = insert;
            }
    
            // 
            reader.readAsText(fileData);
            
        },
        upload: function(){},
    }
}

</script>
